import React from 'react';
import { useState } from 'react';
import '../css/main.css'
import CollectItem from '../comp/CollectItem';
import ScanerCamera from '../comp/ScanerCameraView';
import Scanner from "../comp/Scanner"
import { useEffect } from 'react';
import CameraList from '../comp/CheckCam';



function AskForCameraPermission() {
	/*
	*/
	const askCameraPermission = async () => {
		const stream = await navigator.mediaDevices.getUserMedia({ video: true });
		stream.getTracks().forEach(track => track.stop());
	};
	
	askCameraPermission()
		.then(() => console.log('Permission granted'))
		.catch(error => console.log(error));
		
		navigator.mediaDevices.getUserMedia({ video: true })
		.then(function (stream) {	
		})
		.catch(function (err) {			
			alert("無法開啟鏡頭，或不支援的瀏覽器。");
		});

}
function deleteAllCookies() {
	const cookies = document.cookie.split(";");

	for (let i = 0; i < cookies.length; i++) {
		const cookie = cookies[i];
		const eqPos = cookie.indexOf("=");
		const name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
		document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
	}

}

const finish_callback = () => {
	return (<>
		<div id='finish_overlay_container'>

		</div>
	</>);
};

function MainGame() {
	const [is_camera_open, set_camera_open] = useState(false);
	const [is_finish, set_is_finish] = useState(false);
	//const [footer_text, set_footer_text] = useState("集章樂");
	const qr_list = [
		{ qrKey: "ap7541", title: "繁花之頂", stamp_url: process.env.PUBLIC_URL + "/imgs/s1.png", cover_url: process.env.PUBLIC_URL + "/imgs/c1.png" },
		{ qrKey: "sp4587", title: "花精靈物語伊娃", stamp_url: process.env.PUBLIC_URL + "/imgs/s2.png", cover_url: process.env.PUBLIC_URL + "/imgs/c2.png" },
		{ qrKey: "qr1016", title: "花精靈物語花之隧道", stamp_url: process.env.PUBLIC_URL + "/imgs/s3.png", cover_url: process.env.PUBLIC_URL + "/imgs/c3.png" },
		{ qrKey: "pa1575", title: "舞台活動區", stamp_url: process.env.PUBLIC_URL + "/imgs/s4.png", cover_url: process.env.PUBLIC_URL + "/imgs/c4.png" },
		{ qrKey: "zm6715", title: "花卉景觀區", stamp_url: process.env.PUBLIC_URL + "/imgs/s5.png", cover_url: process.env.PUBLIC_URL + "/imgs/c5.png" },
		{ qrKey: "mn1279", title: "寵物認養專車", stamp_url: process.env.PUBLIC_URL + "/imgs/s6.png", cover_url: process.env.PUBLIC_URL + "/imgs/c6.png" },
		{ qrKey: "gg4566", title: "消防宣導區" ,stamp_url: process.env.PUBLIC_URL + "/imgs/s7.png", cover_url: process.env.PUBLIC_URL + "/imgs/c7.png" },
		{ qrKey: "hf1234", title: "美食區工商產業展" ,stamp_url: process.env.PUBLIC_URL + "/imgs/s8.png", cover_url: process.env.PUBLIC_URL + "/imgs/c8.png" },
		{ qrKey: "id8941", title: "休閒花藝館 舒壓按摩讃" ,stamp_url: process.env.PUBLIC_URL + "/imgs/s9.png", cover_url: process.env.PUBLIC_URL + "/imgs/c9.png" },
		{ qrKey: "ja1287", title: "彰化優鮮 農產展售區" ,stamp_url: process.env.PUBLIC_URL + "/imgs/s10.png", cover_url: process.env.PUBLIC_URL + "/imgs/c10.png" },
		/*
		*/

	];

	function OnScanned(result) {
		console.log("Scanned", result);
		// set cookie
		document.cookie=result+"=1";

		// Scanned on Clear-qrcode
		if(result==="clr876"){
			deleteAllCookies();
		}
	}

	useEffect(() => {
		let x = document.cookie;
		console.log("Cookies" + x);

		// Check for finish
		const timer = setInterval(() => {
			let is_finished_all = true
			qr_list.forEach(element => {
				is_finished_all = is_finished_all && document.cookie.indexOf(element.qrKey) !== -1;
			});

			// If all finish
			if (is_finished_all) {
				clearInterval(timer);
				set_is_finish(true);
			}
		}, 1000); // Check every second

		return () => {
			clearInterval(timer);
		};
	})

	return (
		<div className='main_section'>
			
			{/* Finish All Panel
			{is_finish &&
				<>
					<div id='main_finish_overlay_container'>
						<img className='thumb' src='https://media.istockphoto.com/id/1296078405/vector/vector-isolated-round-completed-label.jpg?s=612x612&w=0&k=20&c=CNkTbrNNNikay9hTXc02OXFKF40XZJp_w2eomM4LxEU=' />
						<span className='big-text' > 集點完成 ! </span>
					</div>
	
				</>
			} 
			*/}

			<div>
				{/* Leave it blank */}
			</div>

			{/* Main Table Content */}
			<div className='main_center_layout'>
				<CameraList/>
				{/* Camera */}
				<>
					{is_camera_open &&
						<div className='main_overlay-container'>
							<Scanner  callback={OnScanned}></Scanner>
							
						</div>
					}
					<button className='outline main_opt_btn white-blue' onClick={() => {
						set_camera_open(!is_camera_open);
						AskForCameraPermission();
					}}>
						開啟鏡頭
					</button>
				</>

				<div className='main_item-container'>
					{/*
					<CollectItem className="main_item" qrKey="sp4587" title="花精靈物語" />
					<CollectItem className="main_item" qrKey="qr1016" title="花精靈物語" />
					<CollectItem className="main_item" qrKey="ap7541" title="繁花之頂" />
					<CollectItem className="main_item" qrKey="pa1575" title="舞台活動區" />
					<CollectItem className="main_item" qrKey="zm6715" title="花卉景觀區" />
					<CollectItem className="main_item" qrKey="mn1279" title="寵物認養專車" />
					*/}
					{qr_list.map((item, index) => (
						<CollectItem className="main_item" qrKey={item.qrKey} title={item.title} stamp_url={item.stamp_url} cover_url={item.cover_url} />
					))}
				</div>

			</div>
			{/* Footer Camera button */}
			<div className='main_footer_panle'>
				{
					!is_finish &&
					<span className='big main_footer_text'>{"集章樂"}</span>
				}
				{
					is_finish && <>
						<span className='big main_footer_text'>{"集章完成"}</span>
						<span className='small main_footer_text'>{"請至服務台兌換小禮品"}</span>
					</>
				}
				{/* 
				<button className='outline main_opt_btn white-blue' onClick={() => {
					set_camera_open(!is_camera_open);
					AskForCameraPermission();
				}}>
					開啟鏡頭
				</button>

				
				<button className='outline main_opt_btn green-white' onClick={() => {
					deleteAllCookies();
					set_is_finish(false);
				}}>
					(測試) 重設
				</button>
				*/}
			</div>

			{/* 
			<div className='main_footer'>
				彰化縣政府xxxx局
			</div>
			*/}

		</div>
	);

}


export default MainGame;